@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/*
* Class used by the hide() and show() functions
*/
.hidden{
    display: none !important;
}
.tasker-quick-wifi {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  
  min-height: 300px;
  width: 250px;
  max-width: 90vw;
  max-height: 90vh;
  overflow: hidden;
  overflow-y: auto;
  background-color: black;
  border-radius: 4px;
  padding: 8px 0;
}

.tasker-quick-wifi__network {
  padding: 8px 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  cursor: pointer;
}

.tasker-quick-wifi__network-name {
  width: 100%;
}

.tasker-quick-wifi__network-name > p {
  color: white;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 4px;
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 80%;
}

.tasker-quick-wifi__network-name > p:last-child {
  margin-bottom: unset;
  font-size: 15px;
  line-height: 15px;
  color: #b4b4b4;
}

.tasker-quick-wifi__network-icon {
  margin-right: 18px;
  position: relative;
}
.tasker-quick-wifi__network-icon > svg {
  color: #111541;
  fill: #111541;
}
.tasker-quick-wifi__network-icon-strength {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.tasker-quick-wifi__network-icon-strength > svg {
  color: #463fc1;
  fill: #463fc1;
}

.tasker-quick-wifi__network-connected {
  border-bottom: 1px solid #252525;
  padding-bottom: 12px;
  margin-bottom: 6px;
}

.tasker-quick-wifi .ripple {
  background: #b4b4b4 !important;
}
.tasker-quick-wifi .rippleContainer {
  width: 100%;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
